@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#App {
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-family: 'Roboto', sans-serif;
}
main {
  width: 80vw;
}
main .content-view {
  height: 65vh;
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ecf0f1;
}
main .content-code {
  height: 35vh;
  background-color: #57606f;
}


#aside {
  height: 100vh;
  background-color: #c8d6e5;

  padding: 15px;

}
#aside .title {
  margin-bottom: 20px;
}
#aside .props {
  display: flex;
  flex-direction: column;
}
#aside .props .prop {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: justify;
  margin-top: 8px;
}
.props .prop .input-block input {
  border: 0;
  border-radius: 4px;
  height: 35px;
  padding: 5px;
  width: 70px;
  line-height: 40px;
  font-size: 15px;
  margin-right: 5px;
}
.props .prop .input-block input[type='range'] {
  width: 100%;
}
.prop .input-block {
  margin: 7px;
  display: flex;
  flex-direction: column;
}
